import GoogleMap from "google-map-react";
import { inject, observer } from "mobx-react";
import React from 'react';
import Tab2Store from "../store/Tab2Store";
import { PathWithIndex } from './PathsRows';
import './PathElement.css'
interface PathElementProps extends PathWithIndex{
  margin: boolean;
  store?: Tab2Store;
}

const PathElement: React.FC<PathElementProps> = ({name, waypoints, margin, index, store}) => {
  return <div style={{
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    width: "min-content",
    color: "#838388",
    marginLeft: margin ? `calc(50vw - 153px)` : "0px"
  }}>
    <div
      className="map-thumbnail"
      onClick={(_) => {
        store?.stack?.push({
          name: "PathDetail",
          parameter: index
        })
      }}
      style={{
      width: "76px",
      height: "76px",
      borderRadius: "38px",
      overflow: "hidden",
    }}>
      {waypoints.length > 0 ? <GoogleMap
        bootstrapURLKeys={{
          key: "AIzaSyADRccaXGi35p8s2W9pydg5WIXBkOuUy3Y",
          libraries: "places"
        }}
        options={{
          zoomControl: false,
          fullscreenControl: false,
          scaleControl: false,
          scrollwheel: false,
          draggable: false,
          streetViewControl: false,
          panControl: false,
          rotateControl: false,
          mapTypeControl: false,
          clickableIcons: false
        }}
        onClick={() => null}
        defaultZoom={15}
        defaultCenter={waypoints[0]}
      /> : <div style={{backgroundColor: "#E1E2E7", height: "100%"}}/>}
    </div>
    {name}
  </div>
}
export default inject('store')(observer(PathElement));