declare type ToggleWithRange = {
  type: "digital" | "range";
  value: RangeValue;
}

const data = [1, 2, 4, 5, 1, 2, 5, 3, 1, 2, 4];

function checkInRange(input: RangeValue, target: number): boolean {
  switch(typeof input){
    case 'number':
      if (input >= target){
        return true;
      } else {
        return false;
      }
    default:
      if (input.lower <= target && input.upper >= target){
        return true;
      } else {
        return false;
      }
  }
}
function matchTime(input: number): {label: string}{
  if (input === 0){
    return {
      label: "지금",
    };
  } else if (input <= 6){
    return {
      label: `${input}시간 후`,
    };
  } else if (input <= 9){
    return {
      label: `${input - 6}일 후`,
    };
  } else {
    return {
      label:`${input - 9}주 후`,
    };
  }
}
