import { useIonAlert } from '@ionic/react';
import { inject, observer } from 'mobx-react';
import React from 'react';
import Tab2Store from '../store/Tab2Store';

const AppendPath: React.FC<{margin: boolean, store?: Tab2Store}> = ({margin, store}) => {
  const [present] = useIonAlert();
  return <div style={{
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    width: "min-content",
    color: "#838388",
    marginLeft: margin ? `calc(50vw - 153px)` : "0px"
  }}>
    <div
      onClick={(_) => present({
        header: "경로 추가",
        message: "경로 이름을 입력해 주세요",
        inputs: [{
          type: 'text',
        }],
        buttons: [{
          text: "취소",
          role: 'cancel',
        }, {
          text: "변경",
          handler: (d) => {
            store?.addPath(d[0])
          }
        }]
      })}
      style={{
      width: "76px",
      height: "76px",
      borderRadius: "38px",
      overflow: "hidden",
      backgroundColor: "#E1E2E7",
      fontFamily: "SF-Pro",
      fontSize: "34px",
      color: "#156BFB",
      textAlign: "center",
      lineHeight: "34px",
      paddingTop: "22px"
    }}>
      􀅼
    </div>
    추가
  </div>
}
export default inject("store")(observer(AppendPath));