import GoogleMap from "google-map-react";
import { observer, inject } from 'mobx-react'
import Tab2Store from "../store/Tab2Store";

type MapProps = {
  store?: Tab2Store
}

const Map: React.FC<MapProps> = ({store}) => {
  const handleApiLoaded: (map: any, maps: any) => void = (map, maps) => {
    store?.setMapApis(map, maps);
  }
  return (<GoogleMap
    bootstrapURLKeys={{
      key: "AIzaSyADRccaXGi35p8s2W9pydg5WIXBkOuUy3Y",
      libraries: "places"
    }}
    defaultZoom={15}
    style={{
      position: "absolute",
      width: "100%",
      height: "100%",
    }}
    onGoogleApiLoaded={({ map, maps }) => handleApiLoaded(map, maps)}
    yesIWantToUseGoogleMapApiInternals
    options={{
      zoomControl: false,
      fullscreenControl: false,
    }}
    defaultCenter={{ lat: 37.5, lng: 127 }}
  />);
}
export default inject('store')(observer(Map));