import React, { CSSProperties } from 'react';
import PathElement from './PathElement';
import AppendPath from './AppendPath';
import { PathWithIndex } from './PathsRows';
import { inject, observer } from 'mobx-react';

const PathsRow: React.FC<{appendAdd: boolean, style?: CSSProperties, elements: PathWithIndex[]}> = ({appendAdd, elements, style = {}}) => {
  return <div style={{
    display: "flex",
    ...style
  }}>
    {elements.map((element, index) => <PathElement
      key={index}
      margin={index === 0 ? false : true}
      {...element}/>)}
    {appendAdd ? <AppendPath margin={true} /> : <div/>}
  </div>
}
export default inject('store')(observer(PathsRow));