import { IonIcon, IonModal, IonButton } from '@ionic/react';
import { useCallback, useState } from 'react';
import { close } from 'ionicons/icons';
import './ScrollableSwipableModal.css';

interface ScrollableSwipableModalProps{
  router: React.MutableRefObject<HTMLIonRouterOutletElement | null>;
  modalInvoke: (setShowModal: (value: boolean) => void) => JSX.Element;
}

const ScrollableSwipableModal: React.FC<ScrollableSwipableModalProps> = ({router, modalInvoke, children}) => {
  const [showModal, setShowModal] = useState(false);
  const callback = useCallback(() => showModal, [showModal]);
  return (
    <div>
      {modalInvoke(setShowModal)}
      <IonModal 
        isOpen={callback()}
        swipeToClose={true}
        presentingElement={router.current || undefined}
        onDidDismiss={() => setShowModal(false)}>
        <IonButton onClick={function (e){setShowModal(false)}} style={{
          height: "unset",
          width: "unset",
          zIndex: "44",
          position: "absolute",
          right: "15px",
          top: "10px",
          "--background-activated": "rgba(150, 150, 150, 0.6)",
          "--background": "rgba(150, 150, 150, 0.6)",
          "--padding-start": "0px",
          "--padding-end": "0px",
          "--border-radius": "100%", "--background-hover": '#000000', "--background-hover-opacity": "0.04",
          fontSize: "14px"}}>
          <IonIcon slot="icon-only" icon={close} style={{margin: "3px"}} />
        </IonButton>
        <div style={{overflow: "scroll"}}>
          {children}
        </div>
      </IonModal>
    </div>
  );
};

export default ScrollableSwipableModal;
