import IonBottomDrawer from '../components/BottomDrawer';
import React from 'react';
import WaypointDetail from '../components/WaypointDetail';
import Tab2Store from '../store/Tab2Store';
import { inject, observer } from 'mobx-react';
import PathsList from '../components/PathsList';
import LocationSearchResult from '../components/LocationSearchResult';
import PathDetail from '../components/PathDetail';
import WaypointList from '../components/WaypointList';
import PlaceDetail from './PlaceDetail';

function matchView(store?: Tab2Store){
  if (store?.stack != null){
    switch(store.stack[store.stack.length - 1].name){
      case "LocationSearchResult": return <LocationSearchResult />;
      case "PathDetail" : return <PathDetail id={store.stack[store.stack.length - 1].parameter} />;
      case "WaypointDetail": return <WaypointDetail {...store.stack[store.stack.length - 1].parameter} />;
      case "WaypointList": return <WaypointList id={store.stack[store.stack.length - 1].parameter}/>;
      case "PlaceDetail": return <PlaceDetail place={store.stack[store.stack.length - 1].parameter} />
      case "PathsList": return <PathsList />;
      default: return <PathsList />;
    }
  }
}
interface DrawerMatchedProps{
  store?: Tab2Store;
}
const DrawerMatched: React.FC<DrawerMatchedProps> = ({store}) => {
  return <IonBottomDrawer style={{display: 'flex', flexDirection: 'column'}}>
    {matchView(store)}
  </IonBottomDrawer>
}
const Drawer = inject('store')(observer(DrawerMatched));
export default Drawer;