import { IonItem, IonLabel, IonList, IonListHeader, IonNote, IonButton, useIonAlert } from '@ionic/react';
import React from 'react';
import SettingIcon from '../components/SettingIcon';
import Tab2Store from '../store/Tab2Store';
import {inject, observer} from "mobx-react";

const PathDetail: React.FC<{id: number, store?: Tab2Store}> = ({id, store}) => {
  const [present] = useIonAlert();
  return <div style={{height: "100%"}}>
    <IonList style={{height: "calc(100% - 60px)", overflow: "scroll", borderTop: "1px solid rgba(0, 0, 0, 0.3)"}}>
      <IonListHeader style={{fontSize: "1em", fontWeight: "normal", color: "#838388", minHeight: "unset", marginTop: "9px", marginBottom: "9px"}}>경로 보기</IonListHeader>
      <div
        style={{
          backgroundColor: "white",
          borderRadius: "8px",
          margin: "16px",
          overflow: "hidden",
        }}>
        <IonItem button onClick={() => present({
          header: "이름 변경",
          message: "경로 이름을 입력해 주세요",
          inputs: [{
            type: 'text',
          }],
          buttons: [{
            text: "취소",
            role: 'cancel',
          }, {
            text: "변경",
            handler: (d) => {
              if (!!store?.paths && store.paths.length > id){
                store.paths[id].name = d[0];
              }
            }
          }]
        })}>
          <SettingIcon style={{backgroundColor: '#007AFF'}}>􀈌
          </SettingIcon>
          <IonLabel>이름 수정</IonLabel>
          <IonNote slot="end">{store?.paths != null && store.paths.length > id ? store.paths[id].name : ''}</IonNote>
        </IonItem>
        <IonItem button onClick={() => store?.stack?.push({
          name: "WaypointList",
          parameter: id
        })}>
          <SettingIcon style={{backgroundColor: '#007AFF'}}>􀻬
          </SettingIcon>
          <IonLabel>경유지 목록</IonLabel>
          <IonNote slot="end" style={{
            fontSize: "1em",
            lineHeight: "1em",
            maxHeight: "1em",
            textOverflow: "ellipsis",
            whiteSpace: "nowrap",
            overflow: "hidden",
            maxWidth: "calc(100% - 130px)"
          }}>{store?.paths != null && store.paths.length > id && store.paths[id].waypoints.length > 0 ? store.paths[id].waypoints[0].name : ''} - {store?.paths != null && store.paths.length > id && store.paths[id].waypoints.length > 0 ? store.paths[id].waypoints[store.paths[id].waypoints.length - 1].name : ''}</IonNote>
        </IonItem>
      </div>
      <div
        style={{
          backgroundColor: "white",
          borderRadius: "8px",
          margin: "16px",
          overflow: "hidden",
        }}>
          <IonButton onClick={(_) => {store!.pathModalIndex = id}} expand="full" style={{margin: "0px", "--background-activated": "#E1E1E1", "--border-radius": "0px", "--background": "transparent", "--color": "#1C7AFC", fontFamily: "SF-Pro", borderBottom: "1px solid #C6C5C9", "--background-hover": '#000000', "--background-hover-opacity": "0.04"}}>􀹳 경로 날씨 조회</IonButton>
          <IonButton expand="full" style={{margin: "0px", "--background-activated": "#E1E1E1", "--border-radius": "0px", "--background": "transparent", "--color": "#F4432C", fontFamily: "SF-Pro", "--background-hover": '#000000', "--background-hover-opacity": "0.04"}} onClick={(_) => {store?.deletePath(id)}}>􀈒 경로 삭제</IonButton>
      </div>
    </IonList>
  </div>
}
export default inject('store')(observer(PathDetail));