import { IonItem, IonLabel, IonList, IonNote, IonButton, IonIcon } from '@ionic/react';
import { inject, observer } from 'mobx-react';
import React from 'react';
import SettingIcon from '../components/SettingIcon';
import Tab2Store from '../store/Tab2Store';
import { close } from 'ionicons/icons';

interface PlaceDetailProps {
  store?: Tab2Store;
  place: any;
}

const PlaceDetail: React.FC<PlaceDetailProps> = ({store, place}) => {
  return <div style={{height: "100%"}}>
  <div style={{
    display: "flex",
    height: "60px",
    padding: "0px 12px",
    alignItems: "center",
    width: "100%",
    justifyContent: "space-between"}}>
    <div style={{
        margin: "0px",
        padding: "0px",
        fontWeight: "bold",
        whiteSpace: "nowrap",
        textOverflow: "ellipsis",
        fontSize: "25.5px",
        maxHeight: "25.5px",
        overflow: "hidden",
        lineHeight: "25.5px"}}>{place.description}
    </div>
    <IonButton onClick={() => store?.stack?.pop()} style={{
      height: "28px",
      width: "28px",
      "--background-activated": "rgba(150, 150, 150, 0.6)",
      "--background": "rgba(150, 150, 150, 0.6)",
      "--padding-start": "0px",
      "--padding-end": "0px",
      "--border-radius": "100%", "--background-hover": '#000000', "--background-hover-opacity": "0.04",
      fontSize: "14px"}}>
      <IonIcon slot="icon-only" icon={close} style={{margin: "3px"}} />
    </IonButton></div>
  <IonList style={{height: "calc(100% - 60px)", overflow: "scroll", borderTop: "1px solid rgba(0, 0, 0, 0.3)"}}>
    {
      function(place){
        return <div>{store!.stack.length > 3 && ["WaypointDetail", "WaypointList", "PathDetail"].includes(store!.stack[store!.stack.length - 3].name) ? <div
          style={{
            backgroundColor: "white",
            borderRadius: "8px",
            margin: "16px",
            overflow: "hidden",
          }}>
          <IonItem button onClick={() => {
            const popped = store!.stack[store!.stack.length - 3];
            if(popped.name === "WaypointDetail"){
              store!.paths[popped.parameter.id].waypoints[popped.parameter.index] = {
                ...store!.paths[popped.parameter.id].waypoints[popped.parameter.index],
                lat: place.geometry.location.lat(),
                lng: place.geometry.location.lng(),
                name: place.description,
              }
            } else {
              store!.paths[popped.parameter].waypoints.push({
                lat: place.geometry.location.lat(),
                lng: place.geometry.location.lng(),
                name: place.description,
                time: new Date()
              });
            }
            store!.stack.splice(store!.stack.length - 2, 2);
          }}>
            <SettingIcon style={{backgroundColor: '#007AFF'}}>􀎪
            </SettingIcon>
            <IonLabel>{store!.stack[store!.stack.length - 3].name === "WaypointDetail" ? "위치 선택" : "위치 추가"}</IonLabel>
            <IonNote slot="end"></IonNote>
          </IonItem>
        </div> : <div/>}
        <div
          style={{
            backgroundColor: "white",
            borderRadius: "8px",
            margin: "16px",
            overflow: "hidden",
          }}>
            <IonButton onClick={() => { store!.weatherModalName = place.description } } expand="full" style={{margin: "0px", "--background-activated": "#E1E1E1", "--border-radius": "0px", "--background": "transparent", "--color": "#007AFF", fontFamily: "SF-Pro", "--background-hover": '#000000', "--background-hover-opacity": "0.04"}}>􀆮 지역 날씨 조회</IonButton>
        </div></div>
      }(place)
    }
  </IonList>
</div>
}
export default inject('store')(observer(PlaceDetail));