import { IonButton, IonIcon } from "@ionic/react";
import React, { useEffect, useRef, useState } from "react";
import { createGesture } from '@ionic/react';
import Tab2Store from '../store/Tab2Store';
import './BottomDrawer.css'
import { inject, observer } from "mobx-react";
import SearchBox from "./SearchBox";
import { close } from 'ionicons/icons';
interface IonBottomDrawerProps{
  store?: Tab2Store;
  style?: React.CSSProperties;
}

const IonBottomDrawer: React.FC<IonBottomDrawerProps> = ({style = {}, children, store}) => {
  const drawerRef = useRef<HTMLDivElement>(null);
  const [currentHeight, setCurrentHeight] = useState<number | null>(null);
  useEffect(() => {
    let c = drawerRef.current;
    if (c != null){
      const gesture = createGesture({
        el: c,
        gestureName: "swipe",
        direction: "y",
        onMove: (event) => {
          if (c != null){
            c.className = "bottom-drawer";
            c.style.height = `${event.currentY >= window.innerHeight * 0.6 ? window.innerHeight * 0.4 - 50 : (event.currentY <= 0 ? window.innerHeight - 50 : (currentHeight || window.innerHeight * 0.4 - 50) - event.deltaY)}px`;
            c.style.maxHeight = `${event.currentY >= window.innerHeight * 0.6 ? window.innerHeight * 0.4 - 50 : (event.currentY <= 0 ? window.innerHeight - 50 : (currentHeight || window.innerHeight * 0.4 - 50) - event.deltaY)}px`;
          }
        },
        onEnd: event => {
          if (c != null){
            if (c.dataset.open !== "true") {
              if (event.deltaY < -30){
                c.className = "bottom-drawer drawer-open";
                c.style.height = "";
                c.dataset.open = "true";
                setCurrentHeight(window.innerHeight - 50);
              } else {
                c.className = "bottom-drawer drawer-close";
                c.style.height = "";
                c.dataset.open = "false";
                setCurrentHeight(window.innerHeight * 0.4 - 50);
              }
            } else {
              if (event.deltaY >= 30){
                c.className = "bottom-drawer drawer-close";
                c.style.height = "";
                c.dataset.open = "false";
                setCurrentHeight(window.innerHeight * 0.4 - 50);
              } else {
                c.className = "bottom-drawer drawer-open";
                c.style.height = "";
                c.dataset.open = "true";
                setCurrentHeight(window.innerHeight - 50);
              }
            }
            c.style.height = "";
          }
        }
      });
      gesture.enable(true);
    }
  })
  const toggleDrawer = () => {
    let c = drawerRef.current;
    if (c != null){
      if (c.dataset.open === "true") {
        c.className = "bottom-drawer drawer-close";
        c.style.height = "";
        c.dataset.open = "false";
      } else {
        c.className = "bottom-drawer drawer-open";
        c.style.height = "";
        c.dataset.open = "true";
      }
    }
  };
  return (<div className="bottom-drawer" ref={drawerRef} style={{
    backgroundColor: "#F5F5F5",
    ...style
  }}>
  <div style={{ textAlign: "center", height: "22px", minHeight: "22px", maxHeight: "22px"}}>
    <IonButton
      size="large"
      style={{ height: 5, '--background': "#696969"}}
      onClick={toggleDrawer}
    />
  </div>
  {
    ["LocationSearchResult", "PathDetail", "PathsList", "WaypointList"].includes(store?.lastOnStack?.name || "") ? <div style={{display: 'flex', height: "60px", paddingRight: "12px", alignItems: "center"}}>
    <SearchBox/>
    {store?.stack?.length !== 1 ? <IonButton onClick={() => store?.stack?.pop()} style={{
      height: "28px",
      width: "28px",
      "--background-activated": "rgba(150, 150, 150, 0.6)",
      "--background": "rgba(150, 150, 150, 0.6)",
      "--padding-start": "0px",
      "--padding-end": "0px",
      "--border-radius": "100%",
      fontSize: "14px"}}>
      <IonIcon slot="icon-only" icon={close} style={{margin: "3px"}} />
    </IonButton>: <div/>}</div> : <div/>
  }
  <div style={{height: "calc(100% - 22px)"}}>
    {children}
  </div>
</div>);
}

export default inject('store')(observer(IonBottomDrawer));