import { IonItem, IonLabel, IonList, IonListHeader, IonItemSliding, IonItemOptions, IonItemOption } from '@ionic/react';
import { inject, observer } from 'mobx-react';
import React from 'react';
import Tab2Store from '../store/Tab2Store';
interface WaypointListProps {
  store?: Tab2Store,
  id: number
}
const WaypointList: React.FC<WaypointListProps> = ({store, id}) => <div style={{height: "100%"}}>
  <IonList style={{
    flexGrow: 1,
    overflow: "scroll",
    borderTop: "1px solid rgba(0, 0, 0, 0.3)"
    }}>
    <IonListHeader style={{fontSize: "1em", fontWeight: "normal", color: "#838388", minHeight: "unset", marginTop: "9px", marginBottom: "9px"}}>경유지 목록</IonListHeader>
    {
      store?.paths != null && store?.paths.length > id ? store.paths[id].waypoints.map((e, index) => {
        return <IonItemSliding key={index}>
          <IonItem button onClick={() => store?.stack != null ? store.stack.push({
            name: "WaypointDetail",
            parameter: { id, index }
          }) : null}>
            <IonLabel>
              <h2>경유지 {index + 1} - {e.name}</h2>
              <p>{e.time.getFullYear()}-{`${e.time.getMonth()}`.padStart(2, '0')}-{`${e.time.getDate()}`.padStart(2, '0')} {`${e.time.getHours()}`.padStart(2, '0')}:{`${e.time.getMinutes()}`.padStart(2, '0')}</p>
            </IonLabel>
          </IonItem>
          <IonItemOptions side="end">
            <IonItemOption onClick={() => store?.paths != null && store?.paths.length > id ? store.paths[id].waypoints.splice(index, 1) : null} color="danger" style={{fontFamily: "SF-Pro"}}>􀈒 삭제</IonItemOption>
          </IonItemOptions>
        </IonItemSliding>
      }) : <div />
    }
  </IonList>
</div>

export default inject('store')(observer(WaypointList));