import { IonList, IonListHeader } from '@ionic/react';
import React from 'react';
import SearchEntity from '../components/SearchEntity';
import { observer, inject } from 'mobx-react'
import Tab2Store from '../store/Tab2Store';

interface LocationSearchResultProps {
  store?: Tab2Store;
}

const LocationSearchResult: React.FC<LocationSearchResultProps> = ({store}) => 
<div style={{height: "100%"}}>
      <IonList style={{height: "calc(100% - 60px)", overflow: "scroll", borderTop: "1px solid rgba(0, 0, 0, 0.3)"}}>
      <IonListHeader style={{fontSize: "1em", fontWeight: "normal", color: "#838388", minHeight: "unset", marginTop: "9px", marginBottom: "9px"}}>검색 결과</IonListHeader>
      {(store?.predictions || []).map((e) => <SearchEntity key={e.place_id} element={e}/>)}
    </IonList>
  </div>
export default inject('store')(observer(LocationSearchResult));