import { IonSearchbar } from '@ionic/react';
import React from 'react';
import { observer, inject } from 'mobx-react'
import Tab2Store from '../store/Tab2Store';
type SearchBoxProps = {
  store?: Tab2Store
}
@inject('store')
@observer
class SearchBox extends React.Component<SearchBoxProps>{
  render(){
    return (
      <IonSearchbar 
        placeholder="검색"
        onIonChange={async ({detail: { value }}) => {
          if((!!value && value.length > 0)) {
            await new Promise((resolve, reject) => this.props.store?.autocompleteService?.getQueryPredictions({
              input: value
            }, async (predictions: any, status: any) => {
              if (status !== 'OK' || !predictions){
                reject(status);
              } else {
                var result = await Promise.all(predictions.map((e: any) => new Promise((resolve, reject) => {
                  this.props.store?.placesService?.getDetails({
                    placeId: e.place_id
                  }, (result: any, status: any) => {
                    if (status !== 'OK'){
                      reject()
                    } else {
                      resolve({
                        ...result,
                        description: e.description
                      });
                    }
                  })
                })));
                this.props.store?.setPredictions(result);
                resolve(null);
              }
            }))
          }
          this.props.store?.search(value || "");
        }}
      />
    );
  }
}
export default SearchBox;