import { IonContent, IonHeader, IonPage, IonTitle, IonToolbar, IonSegment, IonSegmentButton, IonLabel } from '@ionic/react';
import { useState } from 'react';
import './Tab1.css';
import SuggestionSegment from '../components/SuggestionSegment';
import DetailedSegment from '../components/DetailedSegment';

interface Tab1Props{
  router: React.MutableRefObject<HTMLIonRouterOutletElement | null>
}

const Tab1: React.FC<Tab1Props> = ({router}) => {
  const [segment, setSegment] = useState("simple");
  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>수성구 수성동 1가</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent style={{"--background": "linear-gradient(#2A78FD, #294A80)"}} fullscreen>
        <IonHeader collapse="condense">
          <div>
            <IonSegment value={segment} onIonChange={({detail: { value }}) => setSegment(value || "simple")} style={{"--background": "#294D8C", width: "250px", margin: "0 auto"}}>
              <IonSegmentButton value="simple" style={{"--indicator-color": "#297AFB", "--color": "white", fontWeight: "bold"}}>
                <IonLabel>간략히 보기</IonLabel>
              </IonSegmentButton>
              <IonSegmentButton value="detailed" style={{"--indicator-color": "#297AFB", "--color": "white", fontWeight: "bold"}}>
                <IonLabel>상세히 보기</IonLabel>
              </IonSegmentButton>
            </IonSegment>
          </div>
          <IonToolbar style={{"--background": "rgba(0, 0, 0, 0)"}}>
            <IonTitle size="large" style={{color: 'white'}}>수성구 수성동 1가</IonTitle>
          </IonToolbar>
        </IonHeader>
        {
          segment !== 'simple' ? <DetailedSegment/>: <SuggestionSegment router={router}/>
        }
      </IonContent>
    </IonPage>
  );
};

export default Tab1;
