import { inject, observer } from 'mobx-react';
import React from 'react';
import AppendPath from '../components/AppendPath';
import PathsRow from '../components/PathsRow';
import { Path } from '../store/Tab2Store';

export interface PathWithIndex extends Path{
  index: number;
}

const PathsRows: React.FC<{elements: Path[]}> = ({elements}) => {
  const chunk = 3;
  var temp:PathWithIndex[][] = [];
  for(var i = 0; i < elements.length; i+= chunk){
    var tempWithoutIndex = elements.slice(i, Math.min(elements.length, i + chunk))
    temp.push(tempWithoutIndex.map(function (e, index) {
      return{
        ...e,
        index: i + index
      }
    }))
  }

  return <div>
    {temp.map((e, index) => <PathsRow key={index} appendAdd={elements.length % 3 !== 0 && index === temp.length - 1 ? true : false} style={{
      marginTop: index === 0 ? "0px" : "16px"
    }} elements={e} />)}
    {elements.length % 3 === 0 ? <div style={{
      marginTop: temp.length > 0 ? "16px" : "0px"
    }}>
      <AppendPath margin={false} />
    </div> : <div />}
  </div>
}
export default inject('store')(observer(PathsRows));