import { IonItem, IonLabel, IonList, IonNote, IonButton, IonIcon, IonDatetime } from '@ionic/react';
import { inject, observer } from 'mobx-react';
import React from 'react';
import SettingIcon from '../components/SettingIcon';
import Tab2Store from '../store/Tab2Store';
import { close } from 'ionicons/icons';

interface WaypointDetailProps {
  store?: Tab2Store;
  id: number;
  index: number;
}

const WaypointDetail: React.FC<WaypointDetailProps> = ({store, id, index}) => {
  const { name, time } = store?.paths != null && store.paths.length > id && store.paths[id].waypoints.length > index ? store.paths[id].waypoints[index] : {
    name: "",
    time: new Date(),
  }
  return <div style={{height: "100%"}}>
  <div style={{
    display: "flex",
    height: "60px",
    padding: "0px 12px",
    alignItems: "center",
    width: "100%",
    justifyContent: "space-between"}}>
    <h1 style={{
      margin: "0px",
      padding: "0px",
      fontWeight: "bold",
      fontSize: "25.5px",
      lineHeight: "25.5px"}}>경유지 {index + 1} - {name}</h1>
    <IonButton onClick={() => store?.stack?.pop()} style={{
      height: "28px",
      width: "28px", "--background-hover": '#000000', "--background-hover-opacity": "0.04",
      "--background-activated": "rgba(150, 150, 150, 0.6)",
      "--background": "rgba(150, 150, 150, 0.6)",
      "--padding-start": "0px",
      "--padding-end": "0px",
      "--border-radius": "100%",
      fontSize: "14px"}}>
      <IonIcon slot="icon-only" icon={close} style={{margin: "3px"}} />
    </IonButton></div>
  <IonList style={{height: "calc(100% - 60px)", overflow: "scroll", borderTop: "1px solid rgba(0, 0, 0, 0.3)"}}>
    <div
      style={{
        backgroundColor: "white",
        borderRadius: "8px",
        margin: "16px",
        overflow: "hidden",
      }}>
      <IonItem button onClick={() => store?.stack?.push({
        name: "LocationSearchResult"
      })}>
        <SettingIcon style={{backgroundColor: '#007AFF'}}>􀎪
        </SettingIcon>
        <IonLabel>위치 변경</IonLabel>
        <IonNote slot="end">{name}</IonNote>
      </IonItem>
      <IonItem button>
        <SettingIcon style={{backgroundColor: '#007AFF'}}>􀉉
        </SettingIcon>
        <IonLabel>경유날짜 변경</IonLabel>
        <IonDatetime style={{color: "#A6A6A6"}} displayFormat="YYYY-MM-DD" value={time.toISOString()} onIonChange={({detail: {value}}) => {
          if (!!value && store?.paths != null && store.paths.length > id && store.paths[id].waypoints.length > index){
            store.paths[id].waypoints[index].time = new Date(value);
          }
        }} />
      </IonItem>
      <IonItem button>
        <SettingIcon style={{backgroundColor: '#007AFF'}}>􀐬
        </SettingIcon>
        <IonLabel>경유시간 변경</IonLabel>
        <IonDatetime style={{color: "#A6A6A6"}} displayFormat="HH:mm:ss" value={time.toISOString()} onIonChange={({detail: {value}}) => {
          if (!!value && store?.paths != null && store.paths.length > id && store.paths[id].waypoints.length > index){
            store.paths[id].waypoints[index].time = new Date(value);
          }
        }}/>
      </IonItem>
      <IonItem button onClick={() => store!.weatherModalName = name}>
        <SettingIcon style={{backgroundColor: '#838388'}}>􀆮
        </SettingIcon>
        <IonLabel>날씨정보 조회</IonLabel>
      </IonItem>
    </div>
    <div
      style={{
        backgroundColor: "white",
        borderRadius: "8px",
        margin: "16px",
        overflow: "hidden",
      }}>
        <IonButton onClick={() => {
          if (store?.paths != null && store.paths.length > id && store.paths[id].waypoints.length > index){
            store.paths[id].waypoints.splice(index, 1);
            store?.stack?.pop();
          }
        } } expand="full" style={{margin: "0px", "--background-hover": '#000000', "--background-hover-opacity": "0.04", "--background-activated": "#E1E1E1", "--border-radius": "0px", "--background": "transparent", "--color": "#F4432C", fontFamily: "SF-Pro"}}>􀈒 해당 지역 삭제</IonButton>
    </div>
  </IonList>
</div>
}
export default inject('store')(observer(WaypointDetail));