import { IonItem, IonLabel, IonBadge } from '@ionic/react';
import { useState } from 'react';
interface ServiceIntegrationProps{
  name: String
}

const ServiceIntegration: React.FC<ServiceIntegrationProps> = ({name, children}) => {
  const [integrated, setIntegrated] = useState(false);
  return (
    <IonItem onClick={() => setIntegrated(!integrated)}>
      {children}
      <IonBadge style={{fontFamily: 'SF-Pro', '--color': integrated ? '#1C7AFC' : '#8E8E93', '--background': 'rgba(120, 120, 128, 0.16)'}} slot="end">􀉣{integrated ? '연동됨' : '미연동'}</IonBadge>
      <IonLabel>{name}에</IonLabel>
    </IonItem>
  );
};

export default ServiceIntegration;
