import { IonItem, IonLabel } from '@ionic/react';
import { inject, observer } from 'mobx-react';
import React from 'react';
import Tab2Store from '../store/Tab2Store';

interface SearchEntityProps {
  element: any;
  store?: Tab2Store;
}

const SearchEntity:React.FC<SearchEntityProps> = ({element, store}) => <IonItem
  button
  onClick={(e) => {
    if (store != null){
      const location = {
        lat: element.geometry.location.lat(),
        lng: element.geometry.location.lng()
      }
      store.map.setCenter(location)
      store.stack.push({
        name: "PlaceDetail",
        parameter: element
      });
    }
  }}
  key={element.place_id}>
  <IonLabel>{element.description}</IonLabel>
</IonItem>
export default inject('store')(observer(SearchEntity));