import { IonButton, IonContent, IonIcon, IonModal, IonPage, IonTitle, IonToolbar } from '@ionic/react';
import { close } from 'ionicons/icons';
import Map from '../components/MapComponent';
import React from 'react';
import Drawer from '../components/Drawer';
import Tab2Store from '../store/Tab2Store';
import { inject, observer } from 'mobx-react';
import './Tab2.css';
import DetailedSegment from '../components/DetailedSegment';
import PathWeatherInfo from '../components/PathWeatherInfo';

const Tab2: React.FC<{router: React.MutableRefObject<HTMLIonRouterOutletElement | null>, store?: Tab2Store}> = ({router, store}) => {
  return (
    <IonPage>
      <IonContent fullscreen scrollY={false}>
        <Map/>
        <Drawer />
        <IonModal 
          isOpen={store!.weatherModalName !== undefined || store!.pathModalIndex !== undefined}
          swipeToClose={true}
          presentingElement={router.current!}
          onDidDismiss={() => {store!.weatherModalName = undefined; store!.pathModalIndex = undefined;}}>
          <IonButton onClick={function (e){store!.weatherModalName = undefined; store!.pathModalIndex = undefined; }} style={{
            height: "unset",
            width: "unset",
            zIndex: "44", "--background-hover": '#000000', "--background-hover-opacity": "0.04",
            position: "absolute",
            right: "15px",
            top: "10px",
            "--background-activated": "rgba(150, 150, 150, 0.6)",
            "--background": "rgba(150, 150, 150, 0.6)",
            "--padding-start": "0px",
            "--padding-end": "0px",
            "--border-radius": "100%",
            fontSize: "14px"}}>
            <IonIcon slot="icon-only" icon={close} style={{margin: "3px"}} />
          </IonButton>
          <div style={{overflow: "scroll", background: "linear-gradient(#2A78FD, #294A80)", height: "100%"}}>
            <div style={{maxWidth: "calc(100% - 30px)"}}>
              <IonToolbar style={{"--background": "rgba(0, 0, 0, 0)", marginTop: "10px"}}>
                <IonTitle size="large" style={{color: 'white'}}>{store!.weatherModalName || store!.paths[store!.pathModalIndex!]?.name}</IonTitle>
              </IonToolbar>
            </div>
            {store!.weatherModalName !== undefined ? <DetailedSegment /> : (store!.pathModalIndex != null ? <PathWeatherInfo data={store!.paths[store!.pathModalIndex!].waypoints.map((e) => ({
              ...e,
              number: Math.floor(Math.random() * 5)
            }))} index={store!.pathModalIndex!}/> : <div/>)}
          </div>
        </IonModal>
      </IonContent>
    </IonPage>
  );
};

export default inject('store')(observer(Tab2));