import { Redirect, Route } from 'react-router-dom';
import {
  IonApp,
  IonIcon,
  IonLabel,
  IonRouterOutlet,
  IonTabBar,
  IonTabButton,
  IonTabs,
} from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import { map, cog, rainy } from 'ionicons/icons';
import Tab1 from './pages/Tab1';
import Tab2 from './pages/Tab2';
import Tab3 from './pages/Tab3';

/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';
import { setupConfig } from '@ionic/react';

/* Theme variables */
import './theme/variables.css';
import { useRef } from 'react';
import { Provider } from 'mobx-react';
import Tab2Store from './store/Tab2Store';
import InfoProvider from './pages/InfoProvider';
setupConfig({
  rippleEffect: false,
  mode: 'ios'
});
const App: React.FC = () => {
  const routerRef = useRef<HTMLIonRouterOutletElement | null>(null);
  const store = new Tab2Store();
  store.router = routerRef;
  return (
  <IonApp>
    <Provider store={store}>
      <IonReactRouter>
        <IonTabs>
          <IonRouterOutlet ref={routerRef}>
            <Route exact path="/tab1">
              <Tab1 router={routerRef}/>
            </Route>
            <Route exact path="/tab2">
              <Tab2 router={routerRef}/>
            </Route>
            <Route path="/tab3">
              <Tab3 />
            </Route>
            <Route path="/provider">
              <InfoProvider/>
            </Route>
            <Route exact path="/">
              <Redirect to="/tab1" />
            </Route>
          </IonRouterOutlet>
          <IonTabBar slot="bottom">
            <IonTabButton tab="tab1" href="/tab1">
              <IonIcon icon={rainy} />
              <IonLabel>날씨 보기</IonLabel>
            </IonTabButton>
            <IonTabButton tab="tab2" href="/tab2">
              <IonIcon icon={map} />
              <IonLabel>위치/경로</IonLabel>
            </IonTabButton>
            <IonTabButton tab="tab3" href="/tab3">
              <IonIcon icon={cog} />
              <IonLabel>설정</IonLabel>
            </IonTabButton>
          </IonTabBar>
        </IonTabs>
      </IonReactRouter>
    </Provider>
  </IonApp>
)};

export default App;
